@font-face { font-family: Virtuous; src: url('./fonts/Virtuous-Slab.otf')}
@font-face { font-family: Virtuous; font-weight: bold; src: url('./fonts/Virtuous-Slab\ Bold.otf')}

html, body {
  background-color: #000;
  color: #fff;
  overflow-x: hidden;
}

#homepage {
  position: relative;
  height: 100vh;
}

#bkgdVideo {
  position: absolute;
  height: 100vh;
}

#navbar {
  display: flex;
  flex-direction: row;
  #myName {
    font-family: Virtuous;
    font-weight: bold;
    font-size: 20px;
    position: absolute;
    padding-top: 10px;
    top: 5%;
    left: 5%;
    text-transform: lowercase;
    letter-spacing: 8px;
  }
}

#myOpening {
  font-size: 40px;
  position: absolute;
  top: 25%;
  left: 12%;
  span {
    color: #ececa7;
  }
}

.fade-in {
    animation: fade 3s ease-in forwards;
    opacity: 0;
}

@keyframes fade {
    50% {
      opacity: 0.01;
    }
    100% {
        opacity: 0.8;
    }
}

.slide-top {
	  animation: slide-top 3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    opacity: 0;
}

@keyframes slide-top {
  0% {
    -webkit-transform: translateY(100px);
            transform: translateY(100px);
  }
  50% {
    opacity: 0.01;
  }
  100% {
    opacity: 0.8;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}
